<template>
    <div class="container bg2">
        <div class="capital-record">
            <div class="tabs">
                <span @click="toggleTabs(1)" :class="{acti:tabs==1}">今日</span>
                <span @click="toggleTabs(2)" :class="{acti:tabs==2}">昨天</span>
                <span @click="toggleTabs(3)" :class="{acti:tabs==3}">本月</span>
            </div>
            <div class="list" v-if="list">
                <ul v-if="list.length > 0" class="mui-scroll">
                    <li v-for="item in list" :key="item.pk" class="more" @click="gotoLink(item)">
                        <div class="title">
                            <span class="fl">{{ item.type | tixian_type }}</span>
                            <span class="fr">{{item.money > 0 ? `+${item.money}` : `${item.money}`}}元</span>
                        </div>
                        <div class="time">
                            <span class="fl">{{item.time | timer}}</span>
                            <span class="fr" :class="`status-${item.status}`">{{item.status | tixian_status}}{{item.status == 2 ?'，点击查看原因':''}}</span>
                        </div>
                    </li>
                </ul>
                <div v-else class="no-data">没有找到符合查询条件的记录</div>
            </div>
            <loadding v-else></loadding>
        </div>
    </div>
</template>

<script>
    import {lastDay, getToday, getTomorrow, thisMonth } from "@/tool/common/time.js";
    import { mapState } from "vuex";
    export default {
        data(){
            return {
                tabs: 1,
                list: false,
            }
        },
        created(){
            this.getNetData()
        },
        computed:{
            
        },
        methods: {
            gotoLink(item){
                this.$store.commit('User/SET_WITHDRAW', item);
                this.$router.push('/withdrawalRecordSearch');
            },
            toggleTabs(index){
                this.list = false
                this.tabs = index
                this.getNetData();
            },
            getNetData(){
                let data = {
                    ...this.baseUser,
                    pageIndex: 1,
                    pageSize: 60,
                    status:'',
                    startTime: '',
                    endTime: '',
                }
                switch (this.tabs) {
                    case 1:
                        data.startTime = getToday()
                        data.endTime = getTomorrow()
                        break;
                    case 2:
                        data.startTime = lastDay()
                        data.endTime = getToday()
                        break;
                    case 3:
                        var t = thisMonth()
                        data.startTime = t.thisMonthStart
                        data.endTime = t.thisMonthEnd
                        break;
                    default:
                        break;
                }
                this.$api.user.getWithdrawList(data)
                .then(res=>{
                    console.log(res);
                    this.list = res.userWithdrawList;
                    this.mui_scroll('.capital-record .list');
                })
            }
        }
    }
</script>

